const parser = require('./gmcode.peg.js');

function calcCenter([x1, y1], [x2, y2], r) {
    const center1 = {
        x: 0,
        y: 0,
    };
    const center2 = {
        x: 0,
        y: 0,
    };
    const k = (y2 - y1) / (x2 - x1);

    if (k === 0) {
        center1.x = (x1 + x2) / 2;
        center2.x = (x1 + x2) / 2;
        center1.y = y1 + Math.sqrt(r * r - ((x1 - x2) * (x1 - x2)) / 4);
        center2.y = y2 - Math.sqrt(r * r - ((x1 - x2) * (x1 - x2)) / 4);
    } else {
        const kV = -1 / k;
        const mX = (x1 + x2) / 2;
        const mY = (y1 + y2) / 2;
        const a = 1 + kV * kV;
        const b = -2 * mX - kV * kV * (x1 + x2);
        // eslint-disable-next-line no-mixed-operators
        const c = mX * mX + kV * kV * (x1 + x2) * (x1 + x2) / 4 -
            (r * r - ((mX - x1) * (mX - x1) + (mY - y1) * (mY - y1)));

        center1.x = (-1.0 * b + Math.sqrt(b * b - 4 * a * c)) / (2 * a);
        center2.x = (-1.0 * b - Math.sqrt(b * b - 4 * a * c)) / (2 * a);
        center1.y = kV * center1.x - kV * mX + mY;
        center2.y = kV * center2.x - kV * mX + mY;
    }

    return [center1, center2];
}

function rotateAngle([x, y], [cX, cY], r) {
    const xlen = Math.abs(x - cX);
    const ylen = Math.abs(y - cY);
    const angle = (r * r + xlen * xlen - ylen * ylen) / (2 * r * xlen);

    if (angle < 1 && angle > -1) {
        return y - cY > 0 ? Math.acos(angle) : -Math.acos(angle);
    }

    return 0;
}

function startAngle([x, y], [cX, cY], r) {
    const xlen = Math.abs(x - cX);
    const ylen = Math.abs(y - cY);
    const len = Math.sqrt(xlen * xlen + ylen * ylen);
    const angle = (r * r + r * r - len * len) / (2 * r * r);

    if (angle < 1 && angle > -1) {
        return y - cY > 0 ? Math.acos(angle) : -Math.acos(angle);
    }

    return 0;
}

function calcDeltaAngle([x1, y1], [x2, y2], r) {
    const xlen = Math.abs(x1 - x2);
    const ylen = Math.abs(y1 - y2);
    const len = Math.sqrt(xlen * xlen + ylen * ylen);
    const angle = (r * r + r * r - len * len) / (2 * r * r);

    return Math.acos(angle);
}

function checkCoords([x1, y1], [x2, y2], {
    x,
    y,
}, a) {    
    const rsinA = (y2 - y);
    const rcosA = (x2 - x);
    const xRot = rcosA * Math.cos(a) - rsinA * Math.sin(a);
    const xRes = +x.toFixed(4) + (+xRot.toFixed(4));
    const yRot = rsinA * Math.cos(a) + rcosA * Math.sin(a);
    const yRes = +y.toFixed(4) + (+yRot.toFixed(4));
    
    return Math.abs(xRes - x1) < Number.EPSILON  && Math.abs(yRes - y1) < Number.EPSILON ;     
}

class GmCode {
    // eslint-disable-next-line class-methods-use-this
    // when aClockwise is true mean G02
    arcPath([sx, sy, sz], [ex, ey, ez], r, aClockwise = true) {
        const arcPath = [];
        const center = calcCenter([sx, sy], [ex, ey], r);
        let cx = 0;
        let cy = 0;

        let deltaAngle = calcDeltaAngle([sx, sy], [ex, ey], r);

        if (sx === ex && sy === ey) {
            deltaAngle = 0;
        } else if (r < 0) {
            deltaAngle = 2 * Math.PI - deltaAngle;
        }

        if (aClockwise) {
            if (checkCoords([sx, sy], [ex, ey], center[0], deltaAngle)) {
                cx = center[0].x;
                cy = center[0].y;
            } else {
                cx = center[1].x;
                cy = center[1].y;
            }
        }

        let sa = startAngle(
            [cx + Math.abs(r) * Math.cos(Math.PI), cy + Math.abs(r) * Math.sin(Math.PI)],
            [sx, sy],
            r
        );        
        
        if(sa === 0){
            sa = Math.PI;
        }

        const a = rotateAngle([ex, ey], [cx, cy], r);
        const l = deltaAngle > 0 ? (ez - sz) * ((Math.PI * 2) / deltaAngle) : (ez - sz);

        for (let d = 0; d <= 50; d += 1) {
            const t = deltaAngle / 50 * d;
            const angle = Math.PI - t + sa;
            const nx = cx + Math.abs(r) * Math.cos(angle);
            const ny = cy + Math.abs(r) * Math.sin(angle);
            const nz = sz + ((l * (deltaAngle * d / 50)) / Math.PI / 2);
            arcPath.push({
                x: nx,
                y: ny,
                z: nz,
            });
        }

        return arcPath;
    }

    // eslint-disable-next-line class-methods-use-this
    parser(code) {
        return parser.parse(code);
    }

    // eslint-disable-next-line class-methods-use-this
    toPath(parserObj) {
        let path = [];
        const error = [];

        let abCoord = {
            x: 0,
            y: 0,
            z: 0,
        };

        let now = {
            x: 0,
            y: 0,
            z: 0,
        };

        let mode = 0;
        let motion = '0';

        if (parserObj) {
            parserObj.forEach((obj) => {
                if (Object.keys(obj).indexOf('mode') > -1) {
                    mode = obj.mode;
                }

                if (Object.keys(obj).indexOf('motion') > -1) {
                    motion = obj.motion;
                }

                if (obj.absolute) {
                    if (obj.coords) {
                        const {
                            x,
                            y,
                            z,
                            r,
                        } = obj.coords;

                        abCoord = {
                            x: x ? now.x - +x : abCoord.x,
                            y: y ? now.y - +y : abCoord.y,
                            z: z ? now.z - +z : abCoord.z,
                        };
                    } else {
                        error.push({
                            error: 'no coords with G92',
                        });
                    }
                }

                if (obj.coords) {
                    const {
                        x,
                        y,
                        z,
                        r,
                    } = obj.coords;

                    if (!obj.absolute) {
                        const oldCoords = {
                            ...now,
                        };
                        // eslint-disable-next-line no-empty
                        if (mode === 0) {
                            now = {
                                x: x ? abCoord.x + +x : now.x,
                                y: y ? abCoord.y + +y : now.y,
                                z: z ? abCoord.z + +z : now.z,
                            };
                        } else {
                            now = {
                                x: x ? now.x + +x : now.x,
                                y: y ? now.y + +y : now.y,
                                z: z ? now.z + +z : now.z,
                            };
                        }

                        if (motion.match(/2|3/g) && r) {
                            const arc = this.arcPath(
                                [oldCoords.x, oldCoords.y, oldCoords.z],
                                [now.x, now.y, now.z],
                                +r,
                            );
                            path = path.concat(arc);
                        } else if (motion.match(/0|1/g)) {
                            path.push({
                                ...now,
                            });
                        }
                    }
                }
            });
        }

        if (error.length > 0) {
            return error;
        }

        return path;
    }
}

module.exports = GmCode;
